.header{
    height: 60px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap:1px ;
    background: #6c63ff;
}

.items{
    justify-self: center;
}
.items:first-of-type{
    justify-self: normal;
}


@media(max-width:500px){
    .DesktopOnly{
        display: none;
    }
}