.drawerWrapper{
    width: 45px;
    height: 100%;
    box-sizing: border-box;
    padding: 5px;
}

.line{
    background: white;
    height: 8px;
    width: 100%;
    box-sizing: border-box;
    border-radius: 3px;
    border: 1px inset rgb(187,183,183);
    margin: 2px;
    z-index: 10;
}

@media(min-width:500px)
{
    .drawerWrapper{
        display: none;
    }
}