.introductionWrapper{
    display: grid;
    grid-template-columns: 1.5fr 4fr;
    margin-top: -1px;
    background: linear-gradient(to top, #757f9a,#d7dde8);
    height: 90vh;
  
}
.gridItems{
    display: grid;
    grid-template-columns: 1fr;
    grid-column-gap: 1px;
    place-self: center;
}
.introduction{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100px;
}

.info{
    margin: auto;
    text-align: center;
    width: 50%;
    height: 100%;
    box-sizing: border-box;
    padding-left:5% ;
    padding-right: 5%;
    padding-bottom: 10px;
}

.info:first-of-type{
    padding: 0%;
}

.desg{
    font-weight: 500;
    font-size: 15px;
    text-transform: capitalize;
    overflow-wrap: break-word;
}

.name{
    font-size: 50px;
    font-weight: bold;
    text-shadow: 2px 4px 20px gray;
    font-family: 'Sriracha',cursive;
}

.contact{
    padding-left:10px ;
    padding-top:5px ;
    padding-bottom: 5px;
    margin: 10px;
    text-align: left;
    box-shadow: 8px 8px 11px 1px rgb(81,96,116);
    box-sizing: border-box;
    border-bottom: 1px inset black;
    overflow-wrap: break-word;
    border-radius: 5px;
    color: white;
    background: #616161;
}

@media(max-width:850px){

    .introductionWrapper{
        height:100% ;
      min-height: 100%;
      min-width: 100%; 
     
    }
    .gridItems{
        margin-left: -15px;
        margin-top:-60px;
    }
    .gridItems img{
        margin-left:-15px ;
    }

    .name{
        font-size: 30px;
    }
    .whoAmI{
        grid-column-start: 1;
      grid-column-end: 3;
    }
    
    .info{
       
        padding: 0%;
        width :95%;
    }

  
    .desg{
        font-size: 14px;
    }
    .introduction{
        height: 120px;
        flex-direction: column;
    }


}

.title{

    font-weight: bold;
    font-size: 30px;
    box-sizing: border-box;
    padding:5px;
    font-family: 'Sriracha',cursive;
    margin: 10px;
    animation: bounce 8s infinite;
}

@keyframes bounce{

    0%{
        transform: translateY(-70px);
    }
    4%{
        transform: translateY(0px);
    }
    8%{
        transform: translateY(-30px);
    }
    12%{
        transform: translateY(0px);
    }
    16%{
        transform: translateY(-16px);
    }
    20%{
        transform: translateY(0px);
    }

    100%{
        transform: translateY(0px);
    }
}

p{
    font-family: 'Caveat',cursive;
    font-size: 22px;
}