.contentItem{
   margin: auto;
}

.contentItem img{
    width: 700px;
}

.content{

    display: flex;
    flex-wrap: wrap;
    background: linear-gradient(to top,#757f9a,#d7dde8);
    height: 90vh;
}
.card{
padding-left: 10px;
padding-top: 5px;
padding-bottom: 5px;
margin: 10px;
text-align: left;
box-shadow: 8px 8px 11px 1px rgb(81, 96, 116);
box-sizing: border-box;
padding-right: 10px;
border-bottom: 1px inset black;
overflow-wrap: break-word;
border-radius: 5px;
background: #616161;
color: white;
width: 500px;

}

@media(max-width:850px)
{

    .contentItem img{
        width: 420px;
    }
    .contentItem:first-of-type{
        margin-left: -20%;
    }
    .content{
        flex-direction: column;
        height: 100%;
        min-height: 100%;
    }

    .card{
        width: 95%;
    }
    
}