.wrapper{
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    align-items: center;
    box-sizing: border-box;
    padding: 50px;
    background: linear-gradient(to top,#757f9a,#d7dde8);
    height: 90vh;
}

.wrapperItem {
    width: 50%;
}
.wrapperItem img{
    width: 400px;
}

.bulletDesc{
    font-size: 12px;
}
.headingFont{
    color: #a4a0f3;
    font-family: 'Baloo 2';
    font-weight: 900;
}

.cards{
    display: flex;
    margin: auto;
    box-sizing: border-box;
    padding: 5px;
}
.card{
    background: #616161;
    color: white;
    border-radius: 1rem;
    padding: 1.5rem;
    box-shadow: 3px 3px 12px 2px;
    transition: 0.2s;
    height: 420px;
    width: 300px;
}

.card:not(:first-child){
    margin-left: -250px;
}

.card:not(:last-child):hover,
.card:not(:last-child):focus-within{
    transform: translateY(-1rem);
}
.card:not(:last-child):hover ~ .card,.card:not(:last-child):focus-within ~ .card {
    transform: translateX(255px);
}


@media(max-width:850px)
{
    .wrapper{
        flex-direction: column;
        
        height: 100%;
        min-height: 100%;
        padding: 0;
    }
    .wrapperItem {
        width: 100%;
    }
    .wrapperItem img{
        width: 350px;
    }

    .cards{
        flex-direction: column;
        margin: 0px;
        overflow:scroll;
    }

    .card{
        padding: .5rem;
        height: 440px;
    }
    .card:not(:first-child){
        margin-left: 0px;
        margin-top:-370px;
    }

    .card:not(:last-child):hover ~ .card,.card:not(:last-child):focus-within ~ .card {
        transform: translateY(360px);
    }
    
}